@use '../../styles' as *;

.Text {
  &___heading {
    &___xxxxxxl {
      @include heading(96px, 1.25);
    }

    &___xxxxxl {
      @include heading(72px, 1.25);
    }

    &___xxxxl {
      @include heading(60px, 1.25);
    }

    &___xxxl {
      @include heading(48px, 1.25);
    }

    &___xxl {
      @include heading(40px, 1.25);
    }

    &___xl {
      @include heading(36px, 1.142);
    }

    &___lg {
      @include heading(32px, 1.167);
    }

    &___md {
      @include heading(28px, 1.1);
    }

    &___sm {
      @include heading(24px, 1.125);
    }

    &___xs {
      @include heading(20px, 1.167);
    }

    &___xxs {
      @include heading(18px, 1.2);
    }

    &___xxxs {
      @include heading(16px, 1.25);
    }
  }

  &___body {
    &___xl {
      @include body(24px, 2);
    }

    &___lg {
      @include body(1.125rem, 2);
    }

    &___md {
      @include body(1rem, 1.5);
    }

    &___sm {
      @include body(0.875rem, 1.5);
    }

    &___xs {
      @include body(0.75rem, 1.5);
    }
  }

  &___strong {
    &___lg {
      @include strong(1.125rem, 2);
    }

    &___md {
      @include strong(1rem, 1.5);
    }

    &___sm {
      @include strong(0.875rem, 1.5);
    }

    &___xs {
      @include strong(0.75rem, 1.5);
    }
  }

  &___data {
    &___lg {
      @include data(1.125rem, 1.5);
    }

    &___md {
      @include data(1rem, 1.5);
    }

    &___sm {
      @include data(0.875rem, 1.5);
    }

    &___xs {
      @include data(0.75rem, 1.5);
    }
  }

  &___misc {
    &___button {
      font-family: Montserrat;
      font-size: 0.75rem;
      font-style: normal;
      font-weight: 700;
      text-align: center;
    }
  }
  
  &___b___500 {
    color: $black-500 !important;
  }

  &___b___400 {
    color: $blue-400 !important;
  }

  &___g___100 {
    color: $green-100 !important;
  }

  &___g___200 {
    color: $gray-200 !important;
  }

  &___n___900 {
    color: $neutral-900
  }

  &___n___800 {
    color: $neutral-800
  }

  &___n___700 {
    color: $neutral-700
  }

  &___n___600 {
    color: $neutral-600
  }

  &___n___500 {
    color: $neutral-500
  }

  &___n___400 {
    color: $neutral-400
  }

  &___n___300 {
    color: $neutral-300
  }

  &___n___200 {
    color: $neutral-200
  }

  &___n___100 {
    color: $neutral-100
  }

  &___n___50 {
    color: $neutral-50
  }

  &___n___0 {
    color: $neutral-0
  }
}
